.selected-box{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.selected-select{
  background-color: rgba(255,255,255,0.2); 
  border-radius: 16px;
  padding: 15px 25px;
  font-size: 20px;
  margin-bottom: 30px;
}

.selected-button{
  background-color: rgba(255,255,255,0.2); 
  border-radius: 16px;
  padding: 15px 25px;
  text-align: center;
  font-size: 20px;
  margin-bottom: 30px;
}